.App {
    background: linear-gradient(106deg, #495af1 10%, #70bfec 90%);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Inter', sans-serif;
  }
  
  .AppGlass{
    display: grid;
    height: 97%;
    width: 97%;
    border-radius: 2rem;
    background: rgba(255, 255, 255, 0.54);
    overflow: hidden;
    grid-template-columns: 11rem auto 20rem;
    gap: 2rem;
  }
  
  @media screen and (max-width: 1200px){
    .AppGlass{
      grid-template-columns: 10% 40% auto;
      overflow-y: scroll;
      margin-bottom: 1rem;
    }
  }
  
  @media screen and (max-width: 768px){
    .AppGlass{
      grid-template-columns: 1fr;
    }
    .AppGlass::-webkit-scrollbar{
      display: none;
    }
  }